  font-family: "Calligraffitti", cursive;
  font-family: "Mooli", sans-serif;
  user-select: none;
}

body {
  background: antiquewhite;
  margin: 0px;
  height: 100vh;
  background: linear-gradient(rgba(124, 124, 124, 0.2), rgba(124, 124, 124, 0.2)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-repeat: no-repeat;
}

#content {
  height: 100%;
}

#locationTime {
  top: 30px;
  left: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 20px;
}

#currentTime {
  margin: 10px;
  flex: 1;
  display: flex;
  flex-direction: column;
  font-size: larger;
}

#currentTime > #year {
  font-size: x-large;
}

.location {
  font-size: xx-large;
}

.content {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
  color: aliceblue;
}

.temp {
  font-size: xx-large;
  font-weight: bolder;
}

.sliderBox {
  position: absolute;
  top: 10px;
  right: 10px;
  height: 40px;
  display: flex;
  justify-content: flex-end;
}

#box {
  height: 40px;
  border-radius: 20px;
  width: 80px;
  background: rgb(132, 172, 109);
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease all;
}

#txt {
  padding: 0px 10px 0px 10px;
}

#box:hover {
  cursor: pointer;
}

#slider {
  height: 32px;
  width: 32px;
  border: 1px solid gold;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  margin: 0px 5px 0px 5px;
  border-radius: 50%;
  background: green;
  transition: 0.3s ease all;
}

#today {
  padding: 30px;
  display: flex;
  flex-direction: row;
}

#today > div {
  flex: 1;
  display: flex;
  height: 100px;
  justify-content: center;
  align-items: center;
  padding: 0px 10px 0px 10px;
}

#today > div > img {
  height: 50px;
  padding-right: 5px;
}

.title {
  font-size: larger;
  padding-bottom: 7px;
}

#forecastData {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(1px);
}

#forecastData > * {
  flex: 1;
  width: 100%;
  display: flex;
}

::-webkit-scrollbar {
  width: 12px;
  height: 10px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  height: 7px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

#hourlyForecast {
  align-items: center;
  overflow-x: scroll;
}

#hourlyForecast > * {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.forTime {
  font-size: larger;
  font-weight: bolder;
}

.hour {
  width: 14.3vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* .hour > div {
  width: 14.3vw;
} */
.hour > img {
  height: 80px;
}

#dailyForecast > div > .day {
  font-weight: bolder;
  font-size: larger;
}

#dailyForecast > div > .imm {
  display: flex;
  justify-content: center;
  align-items: center;
}

#dailyForecast > div > .imm > div > .max {
  font-size: larger;
}

#dailyForecast > * {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

form {
  position: absolute;
  width: 30vw;
  top: 50px;
  right: 35vw;
  z-index: 1;
  display: flex;
}

form > input {
  box-sizing: border-box;
  outline: none;
  width: 100%;
  height: 40px;
  padding-left: 20px;
  font-size: larger;
  background: inherit;
  border: 2px solid wheat;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.4);
  transition: 0.3s ease all;
  color: aliceblue;
}

form > input:focus {
  border: none;
  border: 2px solid rgb(169, 199, 169);
}

form > button {
  width: 20%;
  background: inherit;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
  color: aliceblue;
  font-weight: bolder;
  transition: 0.3s ease all;
  border: none;
}

form > button:hover {
  background: rgba(255, 255, 255, 0.2);
  color: rgb(183, 199, 212);
}

.loading {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

#loadingImg {
  height: 30vh;
}

.loading > .fetching {
  font-size: 100px;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.rotate {
  animation: rotate 1s linear infinite;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,sCAAA;EACA,gCAAA;EACA,iBAAA;AACF;;AACA;EACE,wBAAA;EACA,WAAA;EACA,aAAA;EACA,wHAAA;EAKA,sBAAA;EACA,4BAAA;AAFF;;AAKA;EACE,YAAA;AAFF;;AAKA;EACE,SAAA;EACA,UAAA;EACA,aAAA;EACA,sBAAA;EACA,6BAAA;EACA,aAAA;AAFF;;AAKA;EACE,YAAA;EACA,OAAA;EACA,aAAA;EACA,sBAAA;EACA,iBAAA;AAFF;;AAKA;EACE,kBAAA;AAFF;;AAKA;EACE,mBAAA;AAFF;;AAIA;EACE,aAAA;EACA,sBAAA;EACA,kBAAA;EACA,YAAA;EACA,gBAAA;AADF;;AAIA;EACE,mBAAA;EACA,mBAAA;AADF;;AAIA;EACE,kBAAA;EACA,SAAA;EACA,WAAA;EACA,YAAA;EACA,aAAA;EACA,yBAAA;AADF;;AAIA;EACE,YAAA;EACA,mBAAA;EACA,WAAA;EACA,8BAAA;EACA,aAAA;EACA,2BAAA;EACA,8BAAA;EACA,mBAAA;EACA,gDAAA;EACA,yBAAA;AADF;;AAIA;EACE,0BAAA;AADF;;AAIA;EACE,eAAA;AADF;;AAIA;EACE,YAAA;EACA,WAAA;EACA,sBAAA;EACA,sCAAA;EACA,uBAAA;EACA,kBAAA;EACA,iBAAA;EACA,yBAAA;AADF;;AAIA;EACE,aAAA;EACA,aAAA;EACA,mBAAA;AADF;;AAIA;EACE,OAAA;EACA,aAAA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,0BAAA;AADF;;AAIA;EACE,YAAA;EACA,kBAAA;AADF;;AAIA;EACE,iBAAA;EACA,mBAAA;AADF;;AAIA;EACE,OAAA;EACA,aAAA;EACA,sBAAA;EACA,oCAAA;EACA,0BAAA;AADF;;AAIA;EACE,OAAA;EACA,WAAA;EACA,aAAA;AADF;;AAGA;EACE,WAAA;EACA,YAAA;AAAF;;AAGA;EACE,oDAAA;EACA,kBAAA;EACA,WAAA;AAAF;;AAGA;EACE,mBAAA;EACA,oDAAA;AAAF;;AAGA;EACE,mBAAA;EACA,kBAAA;AAAF;;AAIA;EACE,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;AADF;;AAIA;EACE,iBAAA;EACA,mBAAA;AADF;;AAIA;EACE,aAAA;EACA,YAAA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;AADF;;AAIA;;GAAA;AAIA;EACE,YAAA;AAFF;;AAKA;EACE,mBAAA;EACA,iBAAA;AAFF;;AAKA;EACE,aAAA;EACA,uBAAA;EACA,mBAAA;AAFF;;AAKA;EACE,iBAAA;AAFF;;AAKA;EACE,OAAA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,sBAAA;AAFF;;AAKA;EACE,kBAAA;EACA,WAAA;EACA,SAAA;EACA,WAAA;EACA,UAAA;EACA,aAAA;AAFF;;AAKA;EACE,sBAAA;EACA,aAAA;EACA,WAAA;EACA,YAAA;EACA,kBAAA;EACA,iBAAA;EACA,mBAAA;EACA,uBAAA;EACA,kBAAA;EACA,oCAAA;EACA,yBAAA;EACA,gBAAA;AAFF;;AAKA;EACE,YAAA;EACA,oCAAA;AAFF;;AAKA;EACE,UAAA;EACA,mBAAA;EACA,oCAAA;EACA,kBAAA;EACA,gBAAA;EACA,mBAAA;EACA,yBAAA;EACA,YAAA;AAFF;;AAKA;EACE,oCAAA;EACA,yBAAA;AAFF;;AAKA;EACE,aAAA;EACA,YAAA;